@font-face {
  font-family: 'JetBrains Mono';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url('./fonts/JetBrainsMono/regular.woff2') format('woff2');
}

@font-face {
  font-family: 'JetBrains Mono';
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url('./fonts/JetBrainsMono/bold.woff2') format('woff2');
}

@font-face {
  font-family: 'Iceland';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url('./fonts/Iceland/regular.woff2') format('woff2');
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'JetBrains Mono', sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

#root {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
}

.digits-with-shadow {
  margin: 0;
  text-shadow: 0 4px 32px rgba(51, 51, 255, .50),
    0 0 8px rgba(51, 51, 255, .25),
    0 0 16px rgba(51, 51, 255, .25);
}
